
'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import { getCurrentUser, User } from '@/lib/api'
import PublicHeader from '@/components/PublicHeader'
import { Button } from "@/components/ui/button"
import { ArrowRight, FileText, CheckCircle, Users, Send } from 'lucide-react'
import Link from 'next/link'
import AdminPanel from '@/components/AdminPanel'
import EditorPanel from '@/components/EditorPanel'
import AuthorDashboard from '@/components/AuthorDashboard'
import CoordinatorDashboard from '@/components/CoordinatorDashboard'
import LoginPage from '@/components/LoginPage'

export default function Home() {
  const router = useRouter()
  const [user, setUser] = useState<User | null>(null)
  const [loading, setLoading] = useState(true)
  const [portal, setPortal] = useState<string>('author') // Default to author if not set

  useEffect(() => {
    // Determine portal from env
    const currentPortal = process.env.NEXT_PUBLIC_PORTAL || 'author'
    setPortal(currentPortal)

    async function checkAuth() {
      const currentUser = await getCurrentUser()
      if (currentUser) {
        setUser(currentUser)

        // Portal Redirection Logic
        if (currentPortal === 'author') {
          // Author Portal (Port 3000)
          // If user is staff, redirect to Staff Portal (Port 3001)
          if (['admin', 'editor', 'coordinator'].includes(currentUser.role)) {
            window.location.href = 'http://localhost:3001'
          }
        } else if (currentPortal === 'staff') {
          // Staff Portal (Port 3001)
          // If user is author, redirect to Author Portal (Port 3000)
          if (currentUser.role === 'author') {
            window.location.href = 'http://localhost:3000'
          }
        }
      }
      setLoading(false)
    }
    checkAuth()
  }, [router])

  if (loading) {
    return <div className="min-h-screen flex items-center justify-center">Loading...</div>
  }

  // RENDER LOGIC

  // STAFF PORTAL (Port 3001)
  if (portal === 'staff') {
    if (!user) {
      return <LoginPage onLogin={setUser} />
    }
    // If logged in (and not redirected), show dashboard
    return (
      <main className="min-h-screen bg-gray-100 dark:bg-gray-900">
        {user.role === 'admin' && <AdminPanel user={user} />}
        {user.role === 'editor' && <EditorPanel user={user} />}
        {user.role === 'coordinator' && <CoordinatorDashboard user={user} />}
        {/* Author dashboard shouldn't be reached here due to redirect, but fallback just in case */}
        {user.role === 'author' && <div className="p-8 text-center">Redirecting to Author Portal...</div>}
      </main>
    )
  }

  // AUTHOR PORTAL (Port 3000) - Default
  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
      <PublicHeader />

      {/* Hero Section */}
      <section className="bg-white dark:bg-gray-800 py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <h1 className="text-4xl md:text-6xl font-extrabold text-gray-900 dark:text-white tracking-tight mb-6">
            Research Paper Management System
          </h1>
          <p className="text-xl text-gray-500 dark:text-gray-300 max-w-3xl mx-auto mb-10">
            Streamline your academic publishing workflow. Submit, review, and publish research papers with ease and efficiency.
          </p>
          <div className="flex justify-center space-x-4">
            <Link href="/submit">
              <Button size="lg" className="px-8 py-6 text-lg">
                Submit Your Paper <ArrowRight className="ml-2 h-5 w-5" />
              </Button>
            </Link>
            <Link href="/#about">
              <Button variant="outline" size="lg" className="px-8 py-6 text-lg">
                Learn More
              </Button>
            </Link>
          </div>
        </div>
      </section>

      {/* Features / Onboarding Section */}
      <section id="about" className="py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-16">
            <h2 className="text-3xl font-bold text-gray-900 dark:text-white">How It Works</h2>
            <p className="mt-4 text-lg text-gray-500 dark:text-gray-400">Simple steps to get your research published.</p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-10">
            <div className="bg-white dark:bg-gray-800 p-8 rounded-xl shadow-sm hover:shadow-md transition-shadow">
              <div className="w-12 h-12 bg-blue-100 dark:bg-blue-900 rounded-lg flex items-center justify-center mb-6">
                <FileText className="h-6 w-6 text-blue-600 dark:text-blue-400" />
              </div>
              <h3 className="text-xl font-semibold mb-4 text-gray-900 dark:text-white">1. Submit Paper</h3>
              <p className="text-gray-500 dark:text-gray-400">
                Upload your manuscript in various formats. Choose from Research, Thesis, or Review paper types.
              </p>
            </div>
            <div className="bg-white dark:bg-gray-800 p-8 rounded-xl shadow-sm hover:shadow-md transition-shadow">
              <div className="w-12 h-12 bg-green-100 dark:bg-green-900 rounded-lg flex items-center justify-center mb-6">
                <Users className="h-6 w-6 text-green-600 dark:text-green-400" />
              </div>
              <h3 className="text-xl font-semibold mb-4 text-gray-900 dark:text-white">2. Peer Review</h3>
              <p className="text-gray-500 dark:text-gray-400">
                Expert reviewers analyze your work and provide constructive feedback to improve quality.
              </p>
            </div>
            <div className="bg-white dark:bg-gray-800 p-8 rounded-xl shadow-sm hover:shadow-md transition-shadow">
              <div className="w-12 h-12 bg-purple-100 dark:bg-purple-900 rounded-lg flex items-center justify-center mb-6">
                <CheckCircle className="h-6 w-6 text-purple-600 dark:text-purple-400" />
              </div>
              <h3 className="text-xl font-semibold mb-4 text-gray-900 dark:text-white">3. Publication</h3>
              <p className="text-gray-500 dark:text-gray-400">
                Once approved, your paper is published and made available to the academic community.
              </p>
            </div>
          </div>
        </div>
      </section>

      {/* Contact Section */}
      <section id="contact" className="bg-white dark:bg-gray-800 py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-16 items-center">
            <div>
              <h2 className="text-3xl font-bold text-gray-900 dark:text-white mb-6">Get in Touch</h2>
              <p className="text-lg text-gray-500 dark:text-gray-400 mb-8">
                Have questions about the submission process or need technical support? Our team is here to help.
              </p>
              <div className="space-y-4">
                <div className="flex items-center text-gray-600 dark:text-gray-300">
                  <span className="font-semibold w-24">Email:</span>
                  <span>support@rpms.edu</span>
                </div>
                <div className="flex items-center text-gray-600 dark:text-gray-300">
                  <span className="font-semibold w-24">Phone:</span>
                  <span>+1 (555) 123-4567</span>
                </div>
                <div className="flex items-center text-gray-600 dark:text-gray-300">
                  <span className="font-semibold w-24">Address:</span>
                  <span>123 University Ave, Academic City</span>
                </div>
              </div>
            </div>
            <div className="bg-gray-50 dark:bg-gray-900 p-8 rounded-xl">
              <form className="space-y-6">
                <div>
                  <label htmlFor="name" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Name</label>
                  <input type="text" id="name" className="w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 dark:bg-gray-800 dark:border-gray-700" />
                </div>
                <div>
                  <label htmlFor="email" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Email</label>
                  <input type="email" id="email" className="w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 dark:bg-gray-800 dark:border-gray-700" />
                </div>
                <div>
                  <label htmlFor="message" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Message</label>
                  <textarea id="message" rows={4} className="w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 dark:bg-gray-800 dark:border-gray-700"></textarea>
                </div>
                <Button className="w-full">
                  Send Message <Send className="ml-2 h-4 w-4" />
                </Button>
              </form>
            </div>
          </div>
        </div>
      </section>

      {/* Footer */}
      <footer className="bg-gray-900 text-white py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <p className="text-gray-400">&copy; 2024 Research Paper Management System. All rights reserved.</p>
        </div>
      </footer>
    </div>
  )
}
